import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-record-checkbox";
import { UPDATE } from "./constants";
import style from "./checklistItem.scss";

const view = (state, dispatch) => {
	const {
		properties: { id, label, completed, checklistId }
	} = state;

	return (
		<div id={id + "-item"}>
			<now-record-checkbox
				name={id + "-input"}
				label={label}
				class={{ "-strike-through": completed }}
				value={completed.toString()}
				onValueChange={() =>
					dispatch(UPDATE, { id, checklistId, completed: !completed })
				}
			/>
		</div>
	);
};

createCustomElement("sn-checklist-item", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		id: {},
		label: {
			reflect: true
		},
		completed: {
			reflect: true,
			default: false
		},
		checklistId: {}
	},
	styles: [style]
});
