import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import map from "lodash/map";
import isEmpty from "lodash/isEmpty";
import {
	LOAD_WITH_DOCUMENT_ID,
	LOAD_WITH_CHECKLIST_ID,
	CHECKLIST_ID_RECEIVED,
	LOAD_SUCCEEDED,
	LOAD_FAILED,
	UPDATE,
	UPDATE_SUCCEEDED,
	UPDATE_FAILED
} from "./constants";
import get from "lodash/get";
import set from "lodash/set";
import { createHttpEffect } from "@servicenow/ui-effect-http";
import { effectWithInterceptors, logErrorEffect } from "./effects";
import "./checklistItem.js";
import style from "./checklist.scss";

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const view = state => {
	const { items } = state;
	const { checklistId, title } = state.properties;

	if (isEmpty(items)) return;

	return (
		<main className="checklist-panel">
			{title && <h2 className="checklist-title">{title}</h2>}
			<article>
				<div id={checklistId + "-checklist"}>
					{map(items, item => (
						<sn-checklist-item
							key={item.id}
							id={item.id}
							label={item.name}
							completed={item.complete}
							checklistId={item.checklistId}
						/>
					))}
				</div>
			</article>
		</main>
	);
};

// This will load the checklist from the newly retrieved checklist id
async function updateChecklistIdEffect({ action, dispatch, state }) {
	const checklistId = get(action, "payload.result[0].sys_id");

	if (!checklistId) {
		dispatch(LOAD_FAILED);
		return;
	}

	set(state, "properties.checklistId", checklistId);
	dispatch(LOAD_WITH_CHECKLIST_ID, { checklistId });
}

createCustomElement("sn-checklist", {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		items: []
	},
	properties: {
		title: {
			reflect: true
		},
		//checklist sys_id
		checklistId: {
			reflect: true
		},
		//record sys id to which the checklist is attached to (example task sys id)
		documentId: {
			reflect: true
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: function({ dispatch, state }) {
			const checklistId = get(state, "properties.checklistId");
			const documentId = get(state, "properties.documentId");

			if (!checklistId && !documentId) {
				console.error("documentId or checklistId is required");
				return;
			}
			if (checklistId) dispatch(LOAD_WITH_CHECKLIST_ID, { checklistId });
			else if (documentId) dispatch(LOAD_WITH_DOCUMENT_ID, { documentId });
		},
		[LOAD_WITH_CHECKLIST_ID]: createHttpEffect(
			"/api/now/checklist/:checklistId",
			{
				method: "GET",
				pathParams: ["checklistId"],
				successActionType: LOAD_SUCCEEDED,
				errorActionType: LOAD_FAILED
			}
		),
		[LOAD_WITH_DOCUMENT_ID]: createHttpEffect(
			"/api/now/table/checklist?sysparm_query=document%3D:documentId",
			{
				method: "GET",
				pathParams: ["documentId"],
				successActionType: CHECKLIST_ID_RECEIVED,
				errorActionType: LOAD_FAILED
			}
		),
		[CHECKLIST_ID_RECEIVED]: updateChecklistIdEffect,
		[LOAD_SUCCEEDED]: ({ action, updateState }) => {
			updateState({
				items: action.payload.result.items
			});
		},
		[LOAD_FAILED]: logErrorEffect,
		[UPDATE]: effectWithInterceptors(
			createHttpEffect("/api/now/checklist/:checklistId/checklistItems/:id", {
				method: "PUT",
				pathParams: ["id", "checklistId"],
				errorActionType: UPDATE_FAILED,
				successActionType: UPDATE_SUCCEEDED
			}),
			[
				context => {
					const completed = get(context, "coeffects.action.payload.completed");

					set(context, "coeffects.action.payload.data.complete", completed);

					return context;
				}
			]
		),
		[UPDATE_SUCCEEDED]: ({ state, action, updateState }) => {
			const index = state.items.findIndex(
				item => item.id === action.payload.result.id
			);

			if (index != -1)
				updateState({
					path: `items[${index}]`,
					value: action.payload.result,
					operation: "set"
				});
		},
		[UPDATE_FAILED]: logErrorEffect
	},
	styles: [style]
});
